<template>
  <div id="car">
        <yd-lightbox>
            <yd-lightbox-img v-for="(item, key) in list" :key="key" :src="item.src"></yd-lightbox-img>
        </yd-lightbox>
        <v-bindClass></v-bindClass>
        <input type="button" value="点击" @click="clickMe">{{data}}
  </div>
</template>

<script>
import BindClass from '@/components/BindClass.vue'
export default {
  data () {
    return {
        data:"aaaaa",
        info:"nono",
        list: [
                {src: 'http://static.ydcss.com/uploads/lightbox/meizu_s1.jpg'},
                {src: 'http://static.ydcss.com/uploads/lightbox/meizu_s2.jpg'},
                {src: 'http://static.ydcss.com/uploads/lightbox/meizu_s3.jpg'},
                {src: 'http://static.ydcss.com/uploads/lightbox/meizu_s4.jpg'},
                {src: 'http://static.ydcss.com/uploads/lightbox/meizu_s5.jpg'},
                {src: 'http://static.ydcss.com/uploads/lightbox/meizu_s6.jpg'}
            ] 
    }
  },

  components: {
    'v-bindClass' : BindClass
  },

  computed: {},

  methods: {
    clickMe () {
      this.data = 'bbbbbb';
    }
  },

  beforeCreate:function(){
        console.log("创建前========")
        console.log(this.data)
        console.log(this.$el)
    },
  created:function(){
      console.log("已创建========")
      console.log(this.info)
      console.log(this.$el)
  },
  beforeMount:function(){
      console.log("载入之前========")
      console.log(this.info)
      console.log(this.$el)
  },
  mounted:function(){
      console.log("已载入========")
      console.log(this.info)
      console.log(this.$el)
  },
  beforeUpdate:function(){
      console.log("更新前========");
      console.log(this.data)
  },
  updated:function(){
      console.log("更新完成========");
      console.log(this.data)
  },
  beforeDestroy:function(){
      console.log("销毁前========")
      console.log(this.info)
      console.log(this.$el)
  },
  destroyed:function(){
      console.log("已销毁========")
      console.log(this.info)
      console.log(this.$el)
  }
}

</script>
<style scoped>

</style>


